    opacity: 0.6;
    color: #fff;
    z-index: 9999;
  }

  .btnNext:hover, .btnPrev:hover, .btnClose:hover {
    opacity: 1;
  }

  .sliderWrap .btnPrev {
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    left: 40px;
  }

.sliderWrap .btnClose {
    top: 40px;
    right: 40px;
  }

.sliderWrap .btnNext {
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
